@import url(https://fonts.googleapis.com/css2?family=Exo:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Exo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  color: #29FD2E;
  position: relative;
  width: 100%;
}

input {
  box-sizing: border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing: border-box;
}

.btn {
  background-color: hsla(240, 2%, 11%, 0.38) !important;
  color: #0076FF !important;
  font-family: 'Exo' !important;
  font-size: 20px !important;
  font-weight: bold !important;
  text-decoration: none;
  border: none !important;
}

.pl-7 {
  padding-left: 48px !important;
}
.pr-7 {
  padding-right: 48px !important;
}

.btn-outline:hover, [open]>.btn-outline {
  color: #29fd2e !important;
  background-color: rgba(3, 4, 3, 0.705) !important;
}


.rotate {
  -webkit-animation: rotation 120s infinite linear;
          animation: rotation 120s infinite linear;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
